.modal {
  backdrop-filter: blur(1rem);
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-dialog {
  max-width: 500px;
  margin: 1.75rem auto;
}

.modal-title {
  font-size: 1.1rem;
  font-weight: 700;
}

.exp-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
